import { parser, trackVariables } from 'lezer-feel';
export function parseExpression(expression, context = {}) {
    return parser.configure({
        top: 'Expression',
        contextTracker: trackVariables(context)
    }).parse(expression);
}
export function parseUnaryTests(expression, context = {}) {
    return parser.configure({
        top: 'UnaryTests',
        contextTracker: trackVariables(context)
    }).parse(expression);
}
